body {
  font-family: Avenir, sans-serif;
  font-weight: 300;
}

.search__bar {
  width: 100%vw;
  border: 1px solid #e1e7ed;
  border-radius: 4px;
  display: flex;
  box-shadow: 0 1px 8px #3d4e611a;
}

.search__bar--big {
  height: 67.66px;
  margin-top: 20px;
  margin-bottom: 20px;
  padding: 0;
  display: flex;
}

.search__bar--small {
  height: 40.66px;
  padding: 0;
  display: flex;
}

.search__bar--big--input, .search__bar--small--input {
  color: #3d4e61;
  font-family: inherit;
  font-weight: 400;
  line-height: inherit;
  border: 0;
  border-radius: 4px;
  flex-shrink: 0;
  align-items: center;
  display: flex;
}

.search__bar--big--input {
  background-image: url("Search.ccd3c07b.svg");
  background-position: left 25px bottom 25px;
  background-repeat: no-repeat;
  background-size: 19px 19px;
  width: calc(100% - 62px);
  padding: 24.33px 0 23.33px 62px;
  font-family: inherit;
  font-size: 16px;
}

.search__bar--small--input {
  background-image: url("Search.ccd3c07b.svg");
  background-position: left 12px bottom 15px;
  background-repeat: no-repeat;
  background-size: 11px 11px;
  width: calc(100% - 33px);
  padding: 10.33px 0 10.33px 33px;
  font-family: inherit;
  font-size: 14px;
}

.search__bar--big :hover {
  background-image: url("Search.ccd3c07b.svg");
  background-position: left 25px bottom 25px;
  background-repeat: no-repeat;
  background-size: 19px 19px;
  font-family: inherit;
  font-style: normal;
