.search__bar--small :hover {
  background-image: url("Search.ccd3c07b.svg");
  background-position: left 12px bottom 15px;
  background-repeat: no-repeat;
  background-size: 11px 11px;
  font-family: inherit;
  font-style: normal;
  box-shadow: 0 3px 8px #3d4e6133;
}

.search__bar--big :focus {
  box-shadow: none;
  filter: drop-shadow(0 4px 4px #00000040);
  color: #3d4e61;
  background-image: url("Search.ccd3c07b.svg");
  background-position: left 25px bottom 25px;
  background-repeat: no-repeat;
  background-size: 19px 19px;
  border: none;
